/** Проверить, что месяц заблокирован по правилу "минимального" */
function minDateDisabled(
  month: number,
  year: number,
  minDate: Date
): boolean {
  const minYear = minDate.getFullYear();
  const minMonth = minDate.getMonth();

  return year === minYear ? month < minMonth : year < minYear;
}

/** Проверить, что месяц заблокирован по правилу "максимального" */
function maxDateDisabled(
  month: number,
  year: number,
  maxDate: Date
): boolean {
  const maxYear = maxDate.getFullYear();
  const maxMonth = maxDate.getMonth();

  return year === maxYear ? month > maxMonth : year > maxYear;
}

/** Проверить, что месяц заблокирован */
export function monthsDisabled(
  month: number,
  year: number,
  minDate?: Date,
  maxDate?: Date
): boolean {
  if (minDate) {
    const isMinDisabled = minDateDisabled(month, year, minDate);
    if (isMinDisabled) return true;
  }

  return maxDate ? maxDateDisabled(month, year, maxDate) : false;
}
